 * name: feeTypeManage
 * description:
 * author: roct
 * date: 22:33 2021/5/27 0027
 */
import { Component, Vue } from 'vue-property-decorator'
import {
  getCostPages,
  addBussCostType,
  changeBussCostType,
  deleteBussCostType,
} from '@/api/financail/financailApi'
import { getDictSelect } from '@/api/manage/sys/housePageApi'
@Component({
  components: {},
})
export default class feeTypeManage extends Vue {
  private pageTitle = '费用类型管理'
  // 查询参数
  private query = {
    current: 1,
    size: 10,
    total: 0,
  }
  // 当前类型
  private currentType = {
    defAmount: '',
    inOrOut: '',
    name: '',
    type: '',
    id: '',
  }
  // 弹窗是否出现
  private dialogVisible = false
  // typeList
  private typeList: any[] = []
  // type的数据字典
  private typeDictList: any[] = []
  public mounted(): void {
    this.getDictSelect()
  }

  /**
   * @Author roct
   * @Description 换页
   * @Date 22:54 2021/5/27 0027
   **/
  private handleCurrentChange(e: number) {
    this.query.current = e
    this.getFeeTypeList()
  }
  /***
   * @Author roct
   * @Description 获取费用类型列表
   * @Date 22:50 2021/5/27 0027
   **/
  private async getFeeTypeList() {
    const res = await getCostPages(this.query)
    this.typeList = res.data.records
    this.query.total = res.data.total
  }
  /**
   * @Author roct
   * @Description 删除费用项
   * @Date 22:58 2021/5/27 0027
   **/
  private async deleteFeeType(scope: any) {
    try {
      await this.$confirm('此操作将永久删除该费用类型, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
      await deleteBussCostType(scope.row.id)
      this.$message('删除成功')
    } catch (e) {}
  }
  /**
   * @Author roct
   * @Description 修改费用类型
   * @Date 23:21 2021/5/27 0027
   **/
  private changeFeeType(scope: any) {
    this.currentType = scope.row
    this.dialogVisible = true
  }
  /**
   * @Author roct
   * @Description 点击弹窗确定
   * @Date 23:24 2021/5/27 0027
   **/
  private async saveFeeTypeClick() {
    this.dialogVisible = false
    if (!this.currentType.id) {
      // 新增
      await addBussCostType(this.currentType)
    } else {
      // 修改
      await changeBussCostType(this.currentType)
    }
    await this.getFeeTypeList()
  }
  /**
   * @Author roct
   * @Description 新增费用类型
   * @Date 23:11 2021/5/27 0027
   **/
  private addNewFeeTypeClick() {
    this.currentType = {
      defAmount: '',
      inOrOut: '',
      name: '',
      type: '',
      id: '',
    }
    this.dialogVisible = true
  }
  /**
   * @Author roct
   * @Description 获取类型数据字典
   * @Date 23:03 2021/5/27 0027
   **/
  private async getDictSelect() {
    const res = await getDictSelect({ dictCode: 'cost_type_type' })
    this.typeDictList = res.data
    await this.getFeeTypeList()
  }
  // 获取当前类型
  get getTypeName() {
    return (value: any) => {
      const item = this.typeDictList.find((target: any) => {
        return (target.value = value)
      })
      if (item) {
        return item.name
      }
      return ''
    }
  }
}
