import apiCore from '@/utils/apiCore'

const HTTP = new apiCore({
  baseURL: process.env.VUE_APP_URL,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
})
// 获取交易流水
export function getFlowList(params: any) {
  return HTTP.get('/buss/receipt/order/page', params)
}

// 获取交易详情
export function getOrderDetailVO(params: { id: string | number }) {
  return HTTP.get('/buss/receipt/order/orderDetailVO', params)
}

// 获取分账方列表
export function getTransPages(params: {
  current: string | number
  size: string | number
}) {
  return HTTP.get('/buss/trans/page', params)
}

// 获取分账方明细
export function getTransDetail(params: { id: string | number }) {
  return HTTP.get('/buss/trans/get', params)
}
// 获取分账账单类型
export function getBillType(params: {
  projectId: string | number
  transId: string | number
}) {
  return HTTP.get('/buss/trans/getBillType', params)
}
// 获取费用类型列表
export function getCostPages(params: { current: number; size: number }) {
  return HTTP.get('/buss/cost/type/page', params)
}
// 新增费用列表
export function addBussCostType(params: {
  defAmount: number | string
  inOrOut: number | string
  name: string
  type: number | string
}) {
  return HTTP.post('/buss/cost/type', params)
}
// 修改费用类型
export function changeBussCostType(params: {
  defAmount: number | string
  inOrOut: number | string
  name: string
  type: number | string
  id: number | string
}) {
  return HTTP.put('/buss/cost/type', params)
}
// 删除费用类型
export function deleteBussCostType(id: number) {
  return HTTP.delete('/buss/cost/type/' + id)
}

// 批量结算审核列表
export function pageCompanyPayList(params: any) {
  return HTTP.get('/buss/merger/pay/pageCompanyPay', params)
}
// 审核通过
export function updateOffPayState(params: any) {
  return HTTP.get('/buss/merger/pay/updateOffPayState', params)
}


